  body: 'Здесь находятся примеры синтаксиса форматирования markdown  поддерживаемые приложением.\n\n⚠️Эту заметку можно смело удалять, примеры markdown синтаксиса можно всегда найти по ссылке в футере.\n\n# Заголовок первого уровня\n\n## Заголовок второго уровня \n\n### Заголовок третьего уровня\n\n#### Заголовок четвёртого уровня\n\n##### Заголовок пятого уровня\n\n###### Заголовок шестого уровня\n\n*Текст курсивом*\n\n_Текст курсивом_\n\n**Жирный текст**\n\n__Жирный текст__\n\n***Текст жирным курсивом***\n\n___Текст жирным курсивом___\n\n~~Зачёркнутый текст~~\n\n***\n---\n___\n\n> Оформление цитатой\nпоследовательных строк\nвнутри одного параграфа\n\n1. Первый пункт\n2. Второй пункт\n3. Третий пункт\n\n\n1. Первый пункт\n1. Второй пункт\n1. Третий пункт\n\n---\n\n1. Первый пункт\n73. Второй пункт\n5. Третий пункт\n\n* Первый пункт\n* Второй пункт\n* Третий пункт\n- Первый пункт\n- Второй пункт\n- Третий пункт\n+ Первый пункт\n+ Второй пункт\n+ Третий пункт\n\n- [x] Отмеченный пункт\n- [ ] Неотмеченный пункт\n\n1. Пункт\n\t1. Подпункт\n\t\t1. Подподпункт\n\n- Пункт\n\t- Подпункт\n\t\t- Подподпункт\n\n\n1. Пункт\n\t- Подпункт\n\t\t* Подподпункт\n\n+ Пункт\n\t1. Подпункт\n\n- Пункт\n  - [x] Отмеченный подпункт\n  - [ ] Неотмеченный подпункт\n    1. Подподпункт\n\n[Тестовая ссылка](https://example.com/) без подсказки\n\n[Тестовая ссылка](https://example.com/ "Всплывающая подсказка") с подсказкой\n\n![Изображение][1]\n\n\n[1]: https://cdn.iconscout.com/icon/free/png-512/free-markdown-1-457956.png?f=avif&w=256 "Логотип Markdown"\n\n![Изображение](https://cdn.iconscout.com/icon/free/png-512/free-markdown-1-457956.png?f=avif&w=256 "Логотип Markdown")\n\n~~~tsx\nconst Editor = (props: EditorProps) => {\n  const onEditField = ({ field, value }: { field: string; value: any }) => {\n    props.onUpdateNote({\n      ...props.activeNote,\n      [field]: value,\n      lastModified: Date.now(),\n    });\n  };\n\n  if (!props.activeNote) return null;\n~~~\n\n`Выделенный текст`',
